import common from '../../common/common.js'

export default {
		data() {
			return {
				userInfo:[],
				id:0,				//优惠券ID
				coupon:[],			// 优惠券信息
			}
		},
		onLoad(option) {
			var that = this
			var userInfo = uni.getStorageSync('userInfo');
						
			
		},
		onPullDownRefresh() {
			var that = this
			uni.showNavigationBarLoading();
			console.log('下拉刷新中...');
						
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();
		},
		methods: {
			jumpUrl(e){
				var that = this
				var url = e.currentTarget.dataset.url
				uni.navigateTo({
					url: url
				});
			},
			//确定核销吗？
			hexiaosubmit(){
				var that = this
				uni.showModal({
					title: '提示',
					content: '确定核销优惠券吗？',
					success: res => {
						if(res.confirm){
							that.hexiaoCoupon();
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			//核销
			hexiaoCoupon(){
				var that = this
				var id = that.id
				var userInfo = uni.getStorageSync('userInfo');
				uni.request({
					url: common.apiUrl + 'shop/hexiaoCoupon',
					method: 'GET',
					data: {
						id:id
					},
					success: res => {
						if(res.data.code == 0){
							uni.showToast({
								title:'核销成功',
								icon:'success',
								duration:1500,
								success:res=>{
									setTimeout(function(){
										uni.navigateTo({
											url:'/pages/user/user'
										})
									},1500)
								}
							})
						}else{
							uni.showToast({
								title:res.data.msg,
								duration:1500
							})
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			//扫码
			scanOrder(){
				var that = this
				uni.scanCode({
					success: function (res) {
						console.log('条码类型：' + res.scanType);
						console.log('条码内容：' + res.result);
						if(res.result){
							that.id = res.result
							that.getHexiaoCouponInfo();
						}
					}
				})
			},
			//获取优惠券信息
			getHexiaoCouponInfo(){
				var that = this
				var id = that.id
				var userInfo = uni.getStorageSync('userInfo');
				uni.request({
					url: common.apiUrl + 'shop/getHexiaoCouponInfo',
					method: 'GET',
					data: {
						id:id,
						users_mobile:userInfo.users_mobile
					},
					success: res => {
						console.log(res)
						if(res.data.code == 0){
							that.coupon = res.data.coupon
							
						}else{
							uni.showToast({
								title:res.data.msg,
								icon:'none',
								duration:1500
							})
						}
						
					},
					fail: () => {},
					complete: () => {}
				});
			}
		},
	
	}
